<template>
  <div class="h-100 w-100 d-flex flex-column justify-content-center">
    <div class="d-flex flex-row flex-fill h-100 justify-content-center">
      <div class="device">
        <h1>{{mega2650Data.humidity}} %</h1>
        <h4>ROOM</h4>
      </div>
      <div class="device">
        <h1>{{d1wifiData.humidity}} %</h1>
        <h4>OUT-DOOR</h4>
      </div>
    </div>
  </div>
</template>

<script>
import { GetMega2560RealTimeData, GetD1WIFIRealTimeData } from '@/apis/database';
export default {
  data() {
    return {
      mega2650Data: {},
      d1wifiData: {},
      fetchDataTimer: null
    }
  },
  mounted() {
    this.fetchDataTimer = setInterval(async () => {
      this.mega2650Data = await GetMega2560RealTimeData();
      this.d1wifiData = await GetD1WIFIRealTimeData();
    }, 1000);
  },
  unmounted() {
    clearInterval(this.fetchDataTimer);
  }
}
</script>

<style>
.device {
  border: 1px solid whitesmoke;
  border-radius: 10px;
  box-shadow: 3px 3px 19px 2px black;
  width: 50%;
  margin: 10px;
  padding: 40px;
  background-color: rgb(43, 43, 43);
}
.device h1 {
  font-size: 80px;
}
.device h4 {
  font-weight: bold;
  text-decoration: underline;
}
</style>